export const styleConfig = (code: any) => {
	const danmakuType = typeOptions().find((i: any) => {
		return i.value === code;
	});
	if (!danmakuType) {
		return {};
	}
	return {
		label: danmakuType.label,
		code,
		type: danmakuType.type,
		widgets: [], // 
		advHtml: "",
		advCss: ""
	};
};
